/* Reset & base */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body, html, #root, .app {
  height: 100%;
  background: #0d0d2b; /* deep navy */
  font-family: 'Poppins', sans-serif;
  color: #d1d5db;
  overflow-x: hidden;
}

/* Scrollbar */
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-thumb {
  background: #8b5cf6;
  border-radius: 10px;
}
::-webkit-scrollbar-track {
  background: #0d0d2b;
}

/* Neon accent colors */
.neon-purple {
  color: #8b5cf6;
}

.neon-blue {
  color: #3b82f6;
}

.neon-yellow {
  color: #facc15;
}

/* Links */
a {
  color: #8b5cf6;
  text-decoration: none;
  transition: color 0.3s ease;
}
a:hover {
  color: #facc15;
  text-decoration: underline;
}

/* Section base */
section {
  padding: 60px 20px;
  max-width: 1200px;
  margin: 0 auto;
}

/* Responsive container for mobile */
@media (max-width: 768px) {
  section {
    padding: 40px 15px;
  }
}

/* Buttons */
button, input[type="submit"] {
  background-color: #8b5cf6;
  color: white;
  border: none;
  padding: 12px 24px;
  font-size: 1rem;
  cursor: pointer;
  border-radius: 30px;
  box-shadow: 0 0 15px #8b5cf6;
  transition: background-color 0.3s ease;
}
button:hover, input[type="submit"]:hover {
  background-color: #3b82f6;
  box-shadow: 0 0 25px #3b82f6;
}

/* Utility classes */
.flex-center {
  display: flex;
  justify-content: center;
  align-items: center;
}

